html{width:100vw;min-width:990px}*{list-style:none;margin:0;padding:0;font-family:"Microsoft YaHei",Arial,Helvetica,sans-serifsans-serif}a{text-decoration:none;color:#090f52;cursor:pointer}body{width:100vw;min-width:990px;background:#F1FAEE}.nav{height:100px;width:100%;background:#A8DADC;position:absolute;top:0;z-index:1}.main{width:90vw;min-width:980px;min-height:780px;height:auto;margin:45px auto 0 auto;background:-webkit-linear-gradient(bottom, #F1FAEE, rgba(255,255,255,0.77));background:linear-gradient(0deg, #F1FAEE, rgba(255,255,255,0.77));border-radius:10px;position:relative;z-index:3}.main .menu{float:left;width:120px;height:600px;box-sizing:border-box;padding:0 0 0 20px;margin:55px 0 0 0;border-right:2px dashed rgba(69,123,157,0.5)}.main .menu .menu-first{font-size:15px;margin:20px 0 0 0}.main .menu .menu-first li{font-size:12px;margin:5px 0 0 10px}.router{width:85%;float:left;padding:5px 0 0 20px}



